import { useEffect, useState } from 'react';
import Navbar from '../components/Navbar';
import ProductCard from '../components/ProductCard';
import CategorySlider from '../components/CategorySlider';

export default function Home() {
  const [userLoggedIn, setUserLoggedIn] = useState(false);
  const [categories, setCategories] = useState([
    "ওয়েবসাইট", "ইউটিউব চ্যানেল", "গেম আইডি", "ডিজিটাল পণ্য"
  ]);
  const [products, setProducts] = useState([]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const token = localStorage.getItem("token");
      if (token) setUserLoggedIn(true);
    }

    setProducts([
      { id:1, title:"ফ্রি ফায়ার আইডি", category:"গেম আইডি", price:15, image:"/images/sample1.jpg", pending:true },
      { id:2, title:"YouTube চ্যানেল", category:"ইউটিউব চ্যানেল", price:150, image:"/images/sample2.jpg", pending:false },
      { id:3, title:"ওয়েবসাইট", category:"ওয়েবসাইট", price:500, image:"/images/sample3.jpg", pending:false }
    ]);
  }, []);

  return (
    <div className="bg-gray-100 dark:bg-gray-900 min-h-screen">
      <Navbar />
      <CategorySlider categories={categories} />

      <div className="p-4 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {products.map((product) => {
          if (product.pending && !userLoggedIn) return null; // Pending logic
          return <ProductCard key={product.id} product={product} userLoggedIn={userLoggedIn} />
        })}
      </div>
    </div>
  );
}
